.playGround{
    display: block;
    position: absolute;
    width: 100%;
    height: calc(100vh - 360px);
}
.playGround .ground{
    position: absolute;
    height: 20%;
    background: #553e20;
    width: 100%;
    bottom: 0;
}
.playGround .gun{
    position: absolute;
    left: 20px;
    bottom: calc(100% + 10px);
    background: #000;
    width: 40px;
    height: 40px;
}
.playGround .enemy{
    position: absolute;
    width: 40px;
    height: 40px;
    background: #000;
    bottom: calc(100% + 10px);
    color: #FFF;
}
.playGround .enemy .health{
    position: absolute;
    background: rgba(0,0,0,0.7);
    left: 50%;
    transform: translateX(-50%);
    bottom: calc(100% + 10px);
    color: #FFF;
    padding: 8px;
    font-size: 15px;
}
.stageCount{
    position: absolute;
    left: 20px;
    top: 20px;
    padding: 20px;
    background: rgba(0,0,0,0.7);
    color: #FFF;
    font-weight: 600;
    font-size: 28px;
}
.gameOver{
    position: absolute;
    z-index: 532432;
    width: 100%;
    height: 100%;
    background: rgba(0,0,0,0.6);
    top: 0;
    left: 0;
}

.gameOver{
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    gap: 32px;
}
.gameOverTitle{
    font-size: 46px;
    font-weight: 600;
    color: #FFF;
}
.gameOver .btns{
    display: flex;
    flex-direction: column;
    gap: 12px;
}
.gameOver .btns .btn{
    background: #2c2c2c;
    color: #FFF;
    width: 240px;
}